import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

export default function AdminDashboard() {
  const navigate = useNavigate();
  const { logout, user } = useAuth();
  const [selectedPath, setSelectedPath] = useState('');

  //     ( )
  const appRoutes = [
    { name: "  (Entry)", path: "/" },
    { name: "  (Login)", path: "/login" },
    { name: "   (Auth)", path: "/auth" },
    { name: "  (Policy)", path: "/policy" },
    { name: "  (Onboarding Individuals)", path: "/onboarding-individuals" },
    { name: "  (Onboarding Companies)", path: "/onboarding-companies" },
    { name: "  -  (Onboarding Visual)", path: "/onboarding-visual" },
    { name: " (Onboarding Illiterate)", path: "/onboarding-illiterate" },
    { name: "  (Onboarding Ultimate)", path: "/onboarding-ultimate" },
    { name: "  (Profile)", path: "/profile" },
  ];

  const handleQuickNav = () => {
    if (selectedPath) navigate(selectedPath);
  };

  return (
    <div className="min-h-screen bg-[#E3DAD0] p-6 flex flex-col items-center select-none">
      {/* Header */}
      <div className="w-full max-w-lg flex items-center justify-between mb-10 bg-white/40 p-4 rounded-3xl border border-white/50 shadow-sm">
        <div className="flex items-center gap-3">
          <img src="/logo.jpg" alt="Logo" className="w-12 h-12 rounded-full border-2 border-[#1A365D]" />
          <div>
            <h2 className="text-[#1A365D] font-black text-lg italic">Careerak Admin</h2>
            <p className="text-[10px] text-[#1A365D]/60 font-bold uppercase tracking-wider">Master Control</p>
          </div>
        </div>
        <button onClick={logout} className="p-3 bg-red-100 text-red-600 rounded-2xl font-black text-xs"></button>
      </div>

      {/* Welcome Card */}
      <div className="w-full max-w-lg bg-[#1A365D] text-white p-8 rounded-[3rem] shadow-2xl mb-8 relative overflow-hidden">
        <div className="relative z-10">
          <h3 className="text-3xl font-black mb-2"> {user?.firstName || ''}!</h3>
          <p className="text-white/60 font-bold text-sm">     .</p>
        </div>
        <div className="absolute -right-10 -bottom-10 w-40 h-40 bg-white/10 rounded-full blur-3xl"></div>
      </div>

      {/*     (Navigation Lab) */}
      <div className="w-full max-w-lg bg-white/80 p-8 rounded-[3rem] shadow-xl border-2 border-white mb-8">
        <div className="flex items-center gap-3 mb-6">
          <span className="text-2xl"></span>
          <h4 className="text-[#1A365D] font-black text-xl">  (Quick Nav)</h4>
        </div>

        <div className="space-y-4">
          <p className="text-[11px] text-[#1A365D]/40 font-bold px-2">        :</p>

          <select
            value={selectedPath}
            onChange={(e) => setSelectedPath(e.target.value)}
            className="w-full p-5 bg-[#E3DAD0]/50 text-[#1A365D] rounded-2xl border-2 border-transparent focus:border-[#1A365D]/20 outline-none font-black text-sm appearance-none cursor-pointer"
            style={{ color: !selectedPath ? '#A1A1A1' : '#1A365D' }}
          >
            <option value="" disabled>--   --</option>
            {appRoutes.map((route, idx) => (
              <option key={idx} value={route.path} style={{color: '#1A365D'}}>{route.name}</option>
            ))}
          </select>

          <button
            onClick={handleQuickNav}
            disabled={!selectedPath}
            className="w-full py-5 bg-[#1A365D] text-white rounded-2xl font-black text-lg shadow-lg active:scale-95 transition-all disabled:opacity-30"
          >
             
          </button>
        </div>
      </div>

      {/* Stats Quick View */}
      <div className="w-full max-w-lg grid grid-cols-2 gap-4">
        <div className="bg-white/60 p-6 rounded-[2rem] text-center shadow-sm border border-white">
          <p className="text-[#1A365D]/40 font-black text-[10px] mb-1 uppercase"> </p>
          <span className="text-[#1A365D] font-black text-2xl">--</span>
        </div>
        <div className="bg-white/60 p-6 rounded-[2rem] text-center shadow-sm border border-white">
          <p className="text-[#1A365D]/40 font-black text-[10px] mb-1 uppercase"> </p>
          <span className="text-[#1A365D] font-black text-2xl">--</span>
        </div>
      </div>

    </div>
  );
}
